 .layui-layer-loading .layui-layer-loading1{width:37px;height:37px;background:url(data:image/gif;base64,) no-repeat}
 .layui-layer-ico16,.layui-layer-loading .layui-layer-loading2{width:32px;height:32px;background:url(data:image/gif;base64,) no-repeat}
 .layui-layer-tips{background:0 0;-webkit-box-shadow:none;box-shadow:none;border:none}
 .layui-layer-tips .layui-layer-content{position:relative;line-height:22px;min-width:12px;padding:8px 15px;font-size:12px;_float:left;border-radius:2px;-webkit-box-shadow:1px 1px 3px rgba(0,0,0,.2);box-shadow:1px 1px 3px rgba(0,0,0,.2);background-color:#000;color:#fff}
 .layui-layer-tips .layui-layer-close{right:-2px;top:-1px}
 .layui-layer-tips i.layui-layer-TipsG{position:absolute;width:0;height:0;border-width:8px;border-color:transparent;border-style:dashed;*overflow:hidden}
 .layui-layer-tips i.layui-layer-TipsB,.layui-layer-tips i.layui-layer-TipsT{left:5px;border-right-style:solid;border-right-color:#000}
 .layui-layer-tips i.layui-layer-TipsT{bottom:-8px}
 .layui-layer-tips i.layui-layer-TipsB{top:-8px}
 .layui-layer-tips i.layui-layer-TipsL,.layui-layer-tips i.layui-layer-TipsR{top:5px;border-bottom-style:solid;border-bottom-color:#000}
 .layui-layer-tips i.layui-layer-TipsR{left:-8px}
 .layui-layer-tips i.layui-layer-TipsL{right:-8px}
 .layui-layer-lan[type=dialog]{min-width:280px}
 .layui-layer-lan .layui-layer-title{background:#4476A7;color:#fff;border:none}
 .layui-layer-lan .layui-layer-btn{padding:5px 10px 10px;text-align:right;border-top:1px solid #E9E7E7}
 .layui-layer-lan .layui-layer-btn a{background:#fff;border-color:#E9E7E7;color:#333}
 .layui-layer-lan .layui-layer-btn .layui-layer-btn1{background:#C9C5C5}
