 .layui-layer-prompt .layui-layer-input{display:block;width:230px;height:36px;margin:0 auto;line-height:30px;padding-left:10px;border:1px solid #e6e6e6;color:#333}
 .layui-layer-prompt textarea.layui-layer-input{width:300px;height:100px;line-height:20px;padding:6px 10px}
 .layui-layer-prompt .layui-layer-content{padding:20px}
 .layui-layer-prompt .layui-layer-btn{padding-top:0}
 .layui-layer-tab{-webkit-box-shadow:1px 1px 50px rgba(0,0,0,.4);box-shadow:1px 1px 50px rgba(0,0,0,.4)}
 .layui-layer-tab .layui-layer-title{padding-left:0;overflow:visible}
 .layui-layer-tab .layui-layer-title span{position:relative;float:left;min-width:80px;max-width:260px;padding:0 20px;text-align:center;overflow:hidden;cursor:pointer}
 .layui-layer-tab .layui-layer-title span.layui-this{height:43px;border-left:1px solid #eee;border-right:1px solid #eee;background-color:#fff;z-index:10}
 .layui-layer-tab .layui-layer-title span:first-child{border-left:none}
 .layui-layer-tabmain{line-height:24px;clear:both}
 .layui-layer-tabmain .layui-layer-tabli{display:none}
 .layui-layer-tabmain .layui-layer-tabli.layui-this{display:block}
 .layui-layer-photos{-webkit-animation-duration:.8s;animation-duration:.8s}
 .layui-layer-photos .layui-layer-content{overflow:hidden;text-align:center}
 .layui-layer-photos .layui-layer-phimg img{position:relative;width:100%;display:inline-block;*display:inline;*zoom:1;vertical-align:top}
 .layui-layer-imgbar,.layui-layer-imguide{display:none}
 .layui-layer-imgnext,.layui-layer-imgprev{position:absolute;top:50%;width:27px;_width:44px;height:44px;margin-top:-22px;outline:0;blr:expression(this.onFocus=this.blur())}
 .layui-layer-imgprev{left:10px;background-position:-5px -5px;_background-position:-70px -5px}
 .layui-layer-imgprev:hover{background-position:-33px -5px;_background-position:-120px -5px}
 .layui-layer-imgnext{right:10px;_right:8px;background-position:-5px -50px;_background-position:-70px -50px}
 .layui-layer-imgnext:hover{background-position:-33px -50px;_background-position:-120px -50px}
 .layui-layer-imgbar{position:absolute;left:0;bottom:0;width:100%;height:32px;line-height:32px;background-color:rgba(0,0,0,.8);background-color:#000\9;filter:Alpha(opacity=80);color:#fff;overflow:hidden;font-size:0}
 .layui-layer-imgtit *{display:inline-block;*display:inline;*zoom:1;vertical-align:top;font-size:12px}
 .layui-layer-imgtit a{max-width:65%;overflow:hidden;color:#fff}
 .layui-layer-imgtit a:hover{color:#fff;text-decoration:underline}
 .layui-layer-imgtit em{padding-left:10px;font-style:normal}
 @-webkit-keyframes layer-bounceOut{100%{opacity:0;-webkit-transform:scale(.7);transform:scale(.7)}30%{-webkit-transform:scale(1.05);transform:scale(1.05)}0%{-webkit-transform:scale(1);transform:scale(1)}}
 @keyframes layer-bounceOut{100%{opacity:0;-webkit-transform:scale(.7);transform:scale(.7)}30%{-webkit-transform:scale(1.05);transform:scale(1.05)}0%{-webkit-transform:scale(1);transform:scale(1)}}
 .layer-anim-close{-webkit-animation-name:layer-bounceOut;animation-name:layer-bounceOut;-webkit-animation-fill-mode:both;animation-fill-mode:both;-webkit-animation-duration:.2s;animation-duration:.2s}
